import React, {useState} from "react";
import { Button, ButtonGroup } from "@mui/material";

import icon_one from '../assets/offerIcon/together.png'
import icon_two from '../assets/offerIcon/free.png'
import icon_three from '../assets/offerIcon/connect.png'

function OfferTrendco() {
    const [showOffer, setShowOffer] = useState(true)

    return ( 
        <div className="offer">
            <h4 className="offer__title">OFERTA</h4>
            <p className="offer__text">Znajd to, czego szukasz lub stwrz niepowtarzaln okazj  Nasza oferta czy szukajcych i oferujcych w jednym miejscu!</p>
            <ButtonGroup className="offer__btn-group" size="large" color="secondary">
                <Button 
                    variant={showOffer ? 'contained' : 'outlined'}
                    onClick={() => setShowOffer(true)}
                    sx={{ width: '50%' }}
                >
                        Szukajcy
                </Button>
                <Button
                    variant={!showOffer ? 'contained' : 'outlined'}
                    onClick={() => setShowOffer(false)}
                    sx={{ width: '50%' }}
                >
                    Oferujcy
                </Button>
            </ButtonGroup>

            {showOffer ? 
                <div className="offer__option-container">
                    <div className="offer__option">
                        <img src={icon_one} alt="" />
                        <h5 className="offer__option-title">SZUKAJ</h5>
                        <p className="offer__option-text">Znajd kogo potrzebujesz w jednym miejscu i o kadej porze</p>
                    </div>
                    <div className="offer__option">
                        <img src={icon_two} alt="" />
                        <h5 className="offer__option-title">NIE PA</h5>
                        <p className="offer__option-text">Nie pacisz za korzystanie z platformy. Korzystarz za darmo do woli</p>
                    </div>
                    <div className="offer__option">
                        <img src={icon_three} alt="" />
                        <h5 className="offer__option-title">POZNAWAJ</h5>
                        <p className="offer__option-text">cz si z ludmi ktrych szukasz, dyskutaj bez granic o swojej pasji</p>
                    </div>
                </div> 
            : null}

            {!showOffer ? 
                <div className="offer__option-container">
                    <div className="offer__option">
                        <img src={icon_one} alt="" />
                        <h5 className="offer__option-title">ROZWIJAJ SI</h5>
                        <p className="offer__option-text">Skup si na jakoci swojej usugi - my pomoemy Ci znale docelowych klientw</p>
                    </div>
                    <div className="offer__option">
                        <img src={icon_two} alt="" />
                        <h5 className="offer__option-title">BEZPATNIE</h5>
                        <p className="offer__option-text">Docieraj do swoich potencjalnych klientw cakowicie bezpatnie</p>
                    </div>
                    <div className="offer__option">
                        <img src={icon_three} alt="" />
                        <h5 className="offer__option-title">ZAINSPIRUJ</h5>
                        <p className="offer__option-text">Rozmawiaj z innymi uytkownikami. Daj im pozna siebie i swj talent</p>
                    </div>
                </div> 
            : null}
        </div>
    );
}

export default OfferTrendco;