import React, {useState} from 'react';

import { loginValidationSchema } from './validation'
import { useFormik } from "formik";
import { Button, TextField, FormControl, Input, InputLabel, InputAdornment, IconButton, FormHelperText } from '@mui/material';
import Visibility from '@mui/icons-material/Visibility';
import VisibilityOff from '@mui/icons-material/VisibilityOff';

import { Link } from 'react-router-dom';

import LogoContainer from "../components/LogoContainer";
import Logo from '../assets/Standard Logo Files/Original on Transparent.png'

function LoginForm(){
    const [showPassword, setShowPassword] = useState(false);

    const handleClickShowPassword = () => setShowPassword((show) => !show);

    const handleMouseDownPassword = (e) => {
        e.preventDefault();
    };

    const formik = useFormik({
        initialValues: {
            loginUsername: '',
            loginPassword: '',
        },
        validationSchema: loginValidationSchema,
        onSubmit: (values, {resetForm}) => {
            resetForm()
        }
    })

    return ( 
        <div className='form__container'>
            <LogoContainer />
            <div className="form__desktop">
                <h2 className="form__desktop--title">Witaj!</h2>
                <p className="form__desktop--text">Nie tra ani chwili - zaloguj si i eksploruj wiat TrendCo</p>
                <div className="form__desktop--image-container">
                    <a href="https://piotr-kuras97.github.io/trendco/"><img src={Logo} alt="" /></a>
                </div>
                <h4 className="form__desktop--slogan">PASJA. <br /> TO WICEJ NI WSZYSTKO.</h4>
            </div>

            <div className="form">
                <form onSubmit={formik.handleSubmit} className='form__form'>
                    <h3 className="form__title">Zaloguj si</h3>
                    <TextField 
                        fullWidth
                        variant="standard"
                        margin="normal"
                        id="loginUsername"
                        name="loginUsername"
                        label="Nazwa uytkownika"
                        value={formik.values.loginUsername}
                        onChange={formik.handleChange}
                        onBlur={formik.handleBlur}
                        error={formik.touched.loginUsername && Boolean(formik.errors.loginUsername)}
                        helperText={formik.touched.loginUsername && formik.errors.loginUsername}
                    />

                    <FormControl fullWidth margin="dense">
                        <InputLabel htmlFor="loginPassword" variant="standard">Haso</InputLabel>
                        <Input 
                            type={showPassword ? 'text' : 'password'}
                            id="loginPassword"
                            name="loginPassword"
                            value={formik.values.loginPassword}
                            onChange={formik.handleChange}
                            onBlur={formik.handleBlur}
                            endAdornment={
                                <InputAdornment position="end">
                                <IconButton
                                  aria-label="toggle password visibility"
                                  onClick={handleClickShowPassword}
                                  onMouseDown={handleMouseDownPassword}
                                >
                                  {showPassword ? <VisibilityOff /> : <Visibility />}
                                </IconButton>
                              </InputAdornment>
                            }
                        />

                        {formik.touched.loginPassword && formik.errors.loginPassword && (
                            <FormHelperText error variant='standard'>{formik.errors.loginPassword}</FormHelperText>
                        )}
                    </FormControl>

                    <Button type="submit" fullWidth variant="contained" style={{ marginTop: '20px' }}>Zaloguj</Button> 

                    <p className="form__register">Nie masz jeszcze konta? <Link to='/register'>Zarejestruj si</Link></p>
                </form>
            </div>
        </div>
     );
}

export default LoginForm;