import React, {useState} from 'react';

import { registerValidationSchema } from './validation'
import { useFormik } from "formik";
import { Button, TextField, Checkbox, FormControlLabel, FormHelperText, Input, InputLabel, InputAdornment ,FormControl, IconButton} from "@mui/material";

import Visibility from '@mui/icons-material/Visibility';
import VisibilityOff from '@mui/icons-material/VisibilityOff';

import LogoContainer from "../components/LogoContainer";
import Logo from '../assets/Standard Logo Files/Original on Transparent.png'
import { Link } from "react-router-dom";

function RegisterForm() {
    const [showPassword, setShowPassword] = useState(false);
    const [showConfirmPassword, setShowConfirmPassword] = useState(false)

    const handleClickShowPassword = () => setShowPassword((show) => !show);
    const handleClickShowConfirmPassword = () => setShowConfirmPassword((show) => !show)
  
    const handleMouseDownPassword = (e) => {
      e.preventDefault();
    };

    const formik = useFormik({
        initialValues: {
            username: '',
            name: '',
            surname: '',
            email: '',
            password: '',
            confirmPassword: '',
            terms: false,
        },
        validationSchema: registerValidationSchema,
        onSubmit: (values, {resetForm}) => {
            resetForm()
        }
    })

    return ( 
        <>
            <div className="form__container">
            <LogoContainer />
            <div className="form__desktop">
                <h2 className="form__desktop--title">Witaj!</h2>
                <p className="form__desktop--text">30 sekund - tyle dzieli Ci od zanurzenia si w wiat ofert na TrendCo</p>
                <div className="form__desktop--image-container">
                    <a href="https://piotr-kuras97.github.io/trendco/"><img src={Logo} alt="" /></a>
                </div>
                <h4 className="form__desktop--slogan">PASJA. <br /> TO WICEJ NI WSZYSTKO.</h4>
            </div>
            <div className="form">
                <form onSubmit={formik.handleSubmit} className="form__form">
                    <h3 className="form__title">Za darmowe konto</h3>
                    <TextField 
                        fullWidth
                        variant="standard"
                        margin="dense"
                        id="username"
                        name="username"
                        label="Nazwa uytkownika"
                        value={formik.values.username}
                        onChange={formik.handleChange}
                        onBlur={formik.handleBlur}
                        error={formik.touched.username && Boolean(formik.errors.username)}
                        helperText={formik.touched.username && formik.errors.username}
                    />

                    <TextField 
                        fullWidth
                        variant="standard"
                        margin="dense"
                        id="name"
                        name="name"
                        label="Imi"
                        value={formik.values.name}
                        onChange={formik.handleChange}
                        onBlur={formik.handleBlur}
                        error={formik.touched.name && Boolean(formik.errors.name)}
                        helperText={formik.touched.name && formik.errors.name}
                    />

                    <TextField 
                        fullWidth
                        variant="standard"
                        margin="dense"
                        id="surname"
                        name="surname"
                        label="Nazwisko"
                        value={formik.values.surname}
                        onChange={formik.handleChange}
                        onBlur={formik.handleBlur}
                        error={formik.touched.surname && Boolean(formik.errors.surname)}
                        helperText={formik.touched.surname && formik.errors.surname}
                    />

                    <TextField 
                        type="email"
                        fullWidth
                        variant="standard"
                        margin="dense"
                        id="email"
                        name="email"
                        label="Adres e-mail"
                        value={formik.values.email}
                        onChange={formik.handleChange}
                        onBlur={formik.handleBlur}
                        error={formik.touched.email && Boolean(formik.errors.email)}
                        helperText={formik.touched.email && formik.errors.email}
                    />
                    <FormControl fullWidth margin="dense">
                        <InputLabel htmlFor="password" variant="standard">Haso</InputLabel>
                        <Input 
                            type={showPassword ? 'text' : 'password'}
                            id="password"
                            name="password"
                            value={formik.values.password}
                            onChange={formik.handleChange}
                            onBlur={formik.handleBlur}
                            endAdornment={
                                <InputAdornment position="end">
                                <IconButton
                                  aria-label="toggle password visibility"
                                  onClick={handleClickShowPassword}
                                  onMouseDown={handleMouseDownPassword}
                                >
                                  {showPassword ? <VisibilityOff /> : <Visibility />}
                                </IconButton>
                              </InputAdornment>
                            }
                        />

                        {formik.touched.password && formik.errors.password && (
                            <FormHelperText error variant='standard'>{formik.errors.password}</FormHelperText>
                        )}
                    </FormControl>
                    

                    <FormControl fullWidth margin="dense">
                        <InputLabel htmlFor="confirmPassword" variant="standard">Powtrz haso</InputLabel>
                        <Input 
                            type={showConfirmPassword ? 'text' : 'password'}
                            id="cofirmPassword"
                            name="confirmPassword"
                            value={formik.values.confirmPassword}
                            onChange={formik.handleChange}
                            onBlur={formik.handleBlur}
                            endAdornment={
                                <InputAdornment position="end">
                                <IconButton
                                  aria-label="toggle password visibility"
                                  onClick={handleClickShowConfirmPassword}
                                  onMouseDown={handleMouseDownPassword}
                                >
                                  {showConfirmPassword ? <VisibilityOff /> : <Visibility />}
                                </IconButton>
                              </InputAdornment>
                            }
                        />

                    {formik.touched.confirmPassword && formik.errors.confirmPassword && (
                        <FormHelperText error variant='standard'>{formik.errors.confirmPassword}</FormHelperText>
                    )}
                    </FormControl>

                    <FormControlLabel
                        control={
                        <Checkbox
                            size="large"
                            name="terms"
                            checked={formik.values.terms}
                            onChange={formik.handleChange}
                            onBlur={formik.handleBlur}
                        />
                        }
                        label="Akceptuj regulamin oraz polityk prywatnoci"
                    />
                    {formik.touched.terms && formik.errors.terms && (
                        <FormHelperText error margin="dense">{formik.errors.terms}</FormHelperText>
                    )}

                    <Button type="submit" fullWidth variant="contained" style={{ marginTop: '20px' }}>Zarejestruj</Button> 

                    <p className="form__login">Masz ju konto? <Link to='/login'>Zaloguj si</Link></p>

                </form>


            </div>
            </div>
        </>
     );
}

export default RegisterForm;