import React, {useEffect} from 'react'
import AOS from 'aos';
import 'aos/dist/aos.css';

import aboutImage from '../assets/images/aboutimage.jpg'

function AboutTrendco() {
    useEffect(() => {
        AOS.init();
      }, [])

    return ( 
       <main className="about">
            <div className="about__text-container">
                <h2 className="about__title" data-aos="fade-right" data-aos-once="true">Nasza misja</h2>
                <p className="about__text-description" data-aos="fade-right" data-aos-once="true">
                    <strong>TRENDCO.</strong> to platforma dla ludzi, ktrzy chc robi to co kochaj najbardziej, to czym si na co dzie fascynuj, <strong>to co kreuje ich wasny wiat.</strong> </p>
                <p className="about__text-description" data-aos="fade-right" data-aos-once="true">
                    <strong>Wierzymy</strong>, e dziki naszej platformie pomoemy Wam stworzy idealne podwaliny do budowania swojej marki.
                </p> 
                <p className="about__text-description" data-aos="fade-right" data-aos-once="true">
                    <strong>Chcemy</strong>, aby kady z Was mg si rozwija w odpowiednim tempie.
                    <br />
                    <strong>Chcemy</strong>, aby kady z Was y wedle wasnych zasad i przekona.
                </p> 
                <p className="about__text-description" data-aos="fade-right" data-aos-once="true">
                    <strong>Jako TRENDCO. pomoemy</strong> znale freelancerom potencjalnych klientw oraz poszukujcym idealnych wykonawcw ich zada.
                </p> 
                <p className="about__text-description" data-aos="fade-right" data-aos-once="true">
                    <strong>czmy si wszyscy i twrzmy rzeczy na pozr niemoliwe!</strong>
                </p> 
                <p className="about__text-description" data-aos="fade-right" data-aos-once="true"><strong>TRENDCO. Team</strong></p>  
            </div>
            <div className="about__image-container">
                <img src={aboutImage} alt="" />
            </div>
       </main>
    );
}

export default AboutTrendco;